  font-weight: 700;
}
p.loading {
  text-align: center;
}
p.byline {
  position: absolute;
  bottom: 0;
  left: 0;
  padding: 0 1rem;
  width: 100%;
  text-align: center;
  font-size: 0.8em;
}
@media only screen and (min-width: 480px) {
  p.byline {
    font-size: 1em;
  }
}
.language {
  position: fixed;
  top: 0;
  right: 0;
  padding: 1rem;
}
.language button {
  margin: 0;
  padding: 0;
  border: none;
  border-bottom: 1px solid #fff;
  background: transparent;
  line-height: 1.4;
}
.language button:hover {
  border-bottom: 2px solid #fffd02;
  box-shadow: none;
}
canvas {
  margin: 0 auto;
}
.bg {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 106.25%;
  height: 300vh;
  background-image: url("../assets/images/spritesheet.png");
  background-position: 0 0;
  background-size: 200% auto;
  opacity: 0;
  transition: opacity 1s;
  -webkit-animation: test 120s linear infinite forwards;
          animation: test 120s linear infinite forwards;
}
@media only screen and (min-width: 640px) {
