      {@html svgFile}
    </span>
  </button> -->

  <div class="ui" class:visible="{$mode === 'explore'}">
    <p class="label">
      {@html lang($copy.filter)}
    </p>
    <Filters />
  </div>

  <button on:click="{() => updateMode('about')}" class="about">About</button>
</div>
