<style src="./../styles/header.styl">header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 3rem;
  background-color: #17037c;
  transform: translate3d(0, 0, 0);
}
.header--intro,
.header--not {
  display: none;
  width: 100%;
}
.header--intro.visible,
.header--not.visible {
  display: flex;
}
.header--intro {
  justify-content: flex-end;
}
@media only screen and (min-width: 640px) {
  .header--intro {
    justify-content: space-between;
    align-items: center;
  }
}
h4 {
  padding-left: 1rem;
}
.mobile {
  display: block;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-left: 3rem;
  padding-right: 1rem;
  width: 100%;
}
@media only screen and (min-width: 960px) {
  .mobile {
    display: none;
  }
}
.desktop {
  display: none;
  margin-left: 3rem;
  padding-right: 1rem;
}
@media only screen and (min-width: 960px) {
  .desktop {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
}
.wordmark {
  position: absolute;
  top: 50%;
  left: 1rem;
  display: block;
  margin: 0;
  width: 7rem;
  transform: translate(0, -50%);
}
@media only screen and (min-width: 640px) {
  .wordmark {
    left: 50%;
    width: 8rem;
    transform: translate(-50%, -50%);
  }
}
.wordmark a {
  display: block;
  width: 100%;
  border: none;
}
.logo {
  position: absolute;
  top: 50%;
  left: 1rem;
  display: block;
  margin: 0;
  width: 1.25rem;
  transform: translate(0, -50%);
}
.logo a {
  display: block;
  width: 100%;
  border: none;
}
p.updated {
  display: none;
  margin: 0;
  margin-left: 0.5rem;
  font-size: 1em;
  line-height: 1;
}
@media only screen and (min-width: 640px) {
  p.updated {
