<style src="./../styles/about.styl">#about {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1001;
  display: none;
  width: 100%;
  background-color: #17037c;
}
#about.visible {
  display: block;
}
#about button {
  position: fixed;
  top: 0.75rem;
  right: 1rem;
}
.content {
  margin: 0 auto;
  padding: 1rem;
  max-width: 40rem;
}
.wordmark {
  display: block;
  margin: 0 auto;
  width: 9rem;
}
.wordmark a {
  display: block;
  width: 100%;
  border: none;
}
.recirc {
  margin-top: 2rem;
  padding: 0 1rem;
  border: 4px solid #fffd02;
}
.terms {
  margin-top: 2rem;
}
.terms .terms__hed {
  margin-bottom: 0.25em;
  font-size: 1.5em;
}
.terms strong {
  color: #fffd02;
}
.terms li {
  margin-bottom: 0.5em;
  line-height: 1.4;
}
.method {
  margin-top: 2rem;
  margin-bottom: 2rem;
  padding-top: 1rem;
  border-top: 1px solid #fffd02;
}

/*# sourceMappingURL=data:application/json;base64, */</style>

<script>
  import lang from "./../components/utils/lang.js";
  import {
    copy,
    lexicon,
    language,
    mode,
    updateMode
  } from "./../components/utils/stores.js";
  import svgX from "./../svg/x-circle.svg";
  import wordmark from "./../svg/pudding/wordmark.svg";

  $: visible = $mode === "about";
</script>

<section id="about" class:visible>
  <div class="content">
    <div class="wordmark">
      <a href="https://pudding.cool" target="_blank">
        {@html wordmark}
      </a>
    </div>
    <div class="byline">
      {@html lang($copy.about.byline)}
    </div>
    <div class="recirc">
      {@html lang($copy.about.recirc)}
    </div>
    <div class="terms">
      <div class="terms__hed">
        {@html lang($copy.terms)}
      </div>
      <ul>
        {#each $lexicon as term}
          <li>
            <strong>{term[`word_${$language}`]}:</strong>
            {term[`description_${$language}`]}
          </li>
        {/each}
      </ul>
    </div>
    <div class="method">
      {@html lang($copy.about.method)}
    </div>

    <button class="alt close" on:click="{updateMode}">
      {@html svgX}
    </button>
  </div>

</section>
